import { AfterViewChecked, Component, HostBinding, OnInit } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { Subscription } from 'rxjs';
import { CommonCustUtils } from 'src/app/commonSRC/customization/psServices/util/common-cust-utils';
import { ConstantCommon } from 'src/app/commonSRC/psServices/models/common-constant';
import { IsysParamObj } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsCommonSettings } from 'src/app/commonSRC/psServices/models/ps-common.settings';
import { PsNavigatorService } from 'src/app/commonSRC/psServices/navigator/ps-navigator.service';
import { CommonUtils } from './../../commonSRC/psServices/models/common-utils';
import { PsCommonService } from './../../commonSRC/psServices/ps-common/ps-common.service';
import { PsApplicationSettings } from 'src/app/commonSRC/psServices/models/ps-app-settings';


@Component({
  selector: 'app-omni-base',
  templateUrl: './omni-base.page.html',
  styleUrls: ['./omni-base.page.scss'],
})
export class OmniBasePage implements OnInit, AfterViewChecked {
  commonProv: PsCommonService;
  isPageDisabled = false;
  baseFormGroup: FormGroup;
  private customizationMap: Map<string, any>; // Modified by Richie for #BUG 1259353
  navigationServices?: PsNavigatorService;
  pagesNavigationInfoSubscription?: Subscription;
  currentOperID;
  screenTranslationOperIds: any;
  @HostBinding('class') psClass;
  constructor() {
    this.commonProv = CommonUtils.injectionHandler(PsCommonService);
    this.navigationServices = CommonUtils.injectionHandler(PsNavigatorService);
    // this.getScreenTranslations();
  }

  init() {
    this.currentOperID = PsCommonSettings.oper_ID;
    this.customizationMap = this.commonProv.returnOperCustomization();
    const navParams: any = this.navigationServices.getAllParams();
    const formDisObj: IsysParamObj = {};
    // let oper = PsCommonSettings.oper_ID;
    // PsCommonSettings.currentListOfSteps = {};
    if (navParams && navParams.readOnlypage === true) {
      formDisObj.IS_READONLY = 1;
      // Modified by Richie for #BUG 1259353
      if (this.customizationMap) {
        this.customizationMap.set(ConstantCommon.FORM_READONLY, formDisObj);
      }
    } else if (this.customizationMap) {
      formDisObj.IS_READONLY = 0;
      this.customizationMap.set(ConstantCommon.FORM_READONLY, formDisObj);
    }
    // End Richie
    
    // AZDB-7529 - Start - AMANAOI250035 - 201195 - The system is not displaying the correct report name - Dev => Muzammil Arif
    this.screenTranslationOperIds = PsApplicationSettings.MAIN_CONFIG.SCREEN_TRANSLATION_OPER ? PsApplicationSettings.MAIN_CONFIG.SCREEN_TRANSLATION_OPER : [];
    if (this.screenTranslationOperIds.length > 0 && this.screenTranslationOperIds.includes(this.currentOperID)) {
      this.getScreenTranslations(this.currentOperID);
    }
    // AZDB-7529 - End - AMANAOI250035 - 201195 - The system is not displaying the correct report name - Dev => Muzammil Arif
  }

  ngOnInit() {
    this.init();
  }

  ionViewDidEnter() {
    this.viewDidEnter();
  }

  ionViewWillLeave() {
    this.viewWillLeave();
  }

  ionViewWillEnter() {
    this.viewWillEnter();
  }

  viewWillEnter() {

  }

  private disableForm(formGroup: FormGroup) {
    // Modified by Richie for #BUG 1259353
    if (formGroup !== undefined && this.customizationMap) {
      const formDisObj: IsysParamObj = this.customizationMap.get(ConstantCommon.FORM_READONLY);
      // End Richie
      if (formDisObj && formDisObj.IS_READONLY === 1 && formGroup.status != 'DISABLED') {
        formGroup.disable();
        Object.keys(formGroup.controls).forEach((key) => {
          this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_READONLY, [key], 1);
        });
      }
    }
  }

  viewDidEnter() {
    this.commonProv.checkViewBS.next({ value: true, page: this, didEnter: true });
    this.commonProv.setCheckViewByOper(this.currentOperID ? this.currentOperID : PsCommonSettings.oper_ID);
    this.commonProv.emitCheckViewByOper(this.currentOperID ? this.currentOperID : PsCommonSettings.oper_ID, { value: true, page: this, didEnter: true });

    this.commonProv.dismissLoading();
    this.disableForm(this.baseFormGroup);
    CommonCustUtils.getAllSessionFcVars(this.baseFormGroup);
    this.commonProv.setFormGroupByOper(this.baseFormGroup); // Added by Zunair For #BUG 1257538
  }

  viewWillLeave() {
    this.commonProv.checkViewBS.next({ value: true, page: this, willLeave: true });
    this.commonProv.setCheckViewByOper(this.currentOperID ? this.currentOperID : PsCommonSettings.oper_ID);
    this.commonProv.emitCheckViewByOper(this.currentOperID ? this.currentOperID : PsCommonSettings.oper_ID, { value: true, page: this, willLeave: true });
  }
  // Edited by Salah #1556226 in some cases the fields were being editable in checker so the setTimeout was 
  // added after all the changeEvents are finished to disable the form 
  ngAfterViewChecked() {
    if (!this.isPageDisabled) {
      setTimeout(() => {
        this.disableForm(this.baseFormGroup);
        this.isPageDisabled = true
      }, 300);
    }
  }

  // AZDB-7529 - Start - AMANAOI250035
  getScreenTranslations(screenOperId: number) {
    const operationId: number = screenOperId ? screenOperId : 2222;
    const activeLanguage: string = PsCommonSettings.activeLanguge ? PsCommonSettings.activeLanguge : 'EN';
    if (operationId && activeLanguage) {
      this.commonProv.language.getOperTranslation(activeLanguage, operationId);
    }
  }
  // AZDB-7529 - End - AMANAOI250035
}
